@mixin base() {
}

&__heading {
    @include base;
    @include sr-only;

    &--visible {
        @include base;
        @include set-text-style($text-style-heading-4);
        
        background-color: $component-secondary-nav-heading-background-color;
        color: $component-secondary-nav-heading-color;
        padding-bottom: space(1);
        padding-left: space(1);
        padding-right: space(1);
        padding-top: space(1);
        margin-bottom: 0;
    }
}